<template>
  <div class="billingRecordsDetails">
    <v-container
      fluid
      grid-list-xl>
      <v-layout>
        <v-flex
          md6
          tag="h3"
          class="mt-0">
          {{ $ln('帳務紀錄搜尋結果') }}-{{ $ln('明細') }}
        </v-flex>
        <v-flex
          md6
          class="text-md-right">
          <v-btn
            small
            color="amber darken-3"
            @click="toBack()">
            <v-icon class="mr-2">
              mdi-arrow-left-thick
            </v-icon>
            {{ $ln('返回') }}
          </v-btn>
        </v-flex>
      </v-layout>

      <div
        v-if="loadingStatus"
        class="text-xs-center">
        <MaterialLoadingPage/>
      </div>
      <div v-else>
        <p class="category font-weight-light">*{{ $ln('查詢類型') }}：{{ $ln(levelItem[level]) }} *{{ $ln('帳務日期') }}：{{ $ln(`%s 至 %s`, start_time, end_time) }}</p>

        <div class="pt-1">
          <v-data-table
            :headers="tableData1.headers"
            :items="tableData1.contents"
            :no-data-text="tableData1.showNoData"
            hide-actions
            class="elevation-1">
            <template
              slot="headerCell"
              slot-scope="{ header }">
              <span>{{ header.text ? $ln(header.text) : header.text }}</span>
            </template>
            <template
              slot="items"
              slot-scope="{ item }">
              <!-- <td class="text-xs-center">{{ item.data1 }}</td> -->
              <td class="text-xs-center">{{ item.SNo }}</td>
              <td class="text-xs-center">{{ item.l_id }}</td>
              <td class="text-xs-center">{{ item.f_id }}</td>
              <td class="text-xs-center">{{ item.preamount }}</td>
              <td class="text-xs-center">{{ item.changeAmount }}</td>
              <td class="text-xs-center">{{ item.after }}</td>
              <td class="text-xs-center">{{ item.access_type }}</td>
              <td class="text-xs-center">{{ item.status }}</td>
              <td class="text-xs-center">{{ item.ProcTime }}</td>
            </template>
          </v-data-table>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'BillingRecordsDetails',
  data () {
    return {
      i18n: {},
      // 載入資料列表
      tableData1: {
        showNoData: '目前無資料',
        headers: [
          // { text: '', value: 'data1', align: 'center' },
          { text: '遊戲類型', value: 'SNo', align: 'center', sortable: false },
          { text: '轉入帳號', value: 'l_id', align: 'center', sortable: false },
          { text: '轉帳帳號', value: 'f_id', align: 'center', sortable: false },
          { text: '轉帳前金額', value: 'preamount', align: 'center', sortable: false },
          { text: '異動金額', value: 'changeAmount', align: 'center', sortable: false },
          { text: '餘額', value: 'after', align: 'center', sortable: false },
          { text: '動作狀態', value: 'access_type', align: 'center', sortable: false },
          { text: '轉帳狀態', value: 'status', align: 'center', sortable: false },
          { text: '轉帳時間', value: 'ProcTime', align: 'center', sortable: false }
        ],
        contents: [
          // { value: false, data1: 1, data2: '皇家', data3: 'A61', data4: 'system', data5: '6,000.00元', data6: '-1,000.00元', data7: '5,000.00元', data8: '撈額度', data9: '正常', data10: '2019-01-29 18:38:27' },
          // { value: false, data1: 2, data2: '皇家', data3: 'A61', data4: 'system', data5: '55,000.00元', data6: '-50,000.00元', data7: '5,000.00元', data8: '撈額度', data9: '正常', data10: '2019-01-25 13:44:26' },
          // { value: false, data1: 3, data2: '皇家', data3: 'A61', data4: 'A51', data5: '5,000.00元', data6: '50,000.00元', data7: '55,000.00元', data8: '增額', data9: '正常', data10: '2019-01-23 14:42:38' }
        ]
      },
      levelItem: {
        1: '會員',
        2: '代理',
        3: '總代理',
        4: '股東',
        5: '大股東',
        6: '總監',
        7: '大總監'
      },
      level: this.$route.query.level,
      account: this.$route.query.account,
      start_time: this.$route.query.start_time,
      end_time: this.$route.query.end_time,
      // 頁面載入狀態
      loadingStatus: false
    }
  },
  computed: {
    ...mapGetters({
      getDetailsState: 'report/getDetailsState'
    })
  },
  watch: {
    getDetailsState (payload) {
      console.log(payload)
      this.tableData1.contents = payload.result
    }
  },
  mounted () {
    this.loadDetails()
  },
  methods: {
    ...mapActions({
      getDetails: 'api/getDetails'
    }),
    // 載入帳務紀錄明細
    loadDetails (page, rowsPerPage) {
      // 載入狀態(開始)
      this.loadingStatus = true
      let params = {
        account: this.$route.query.account,
        start_time: this.$route.query.start_time,
        end_time: this.$route.query.end_time
      }
      this.getDetails(params)
        .then(res => {
          if (Object.prototype.toString.call(res) === '[object Error]' &&
                res.hasOwnProperty('response') &&
                res.response.status !== 200) {
                this.toBack()
            console.error(res.response)
          } else if (res.status === 200) {
            console.log('回傳成功')
          }
          // 載入狀態(結束)
          this.loadingStatus = false
        })
        .catch(err => {
          console.log(err)
          // 載入狀態(結束)
          this.loadingStatus = false
          this.toBack()
        })
    },
    toBack () {
      this.$router.push({
        path: '/manage/report/billing-records/Result',
        query: {
          level: this.$route.query.level,
          account: this.$route.query.account,
          start_time: this.$route.query.start_time,
          end_time: this.$route.query.end_time }
      })
    }
  }
}
</script>

<style lang="scss">
</style>
